import React, { Component } from 'react';
import './App.css';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';

class Header extends Component{
  render() {

    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">Loan Calculator</h1>
          <p>Find the quickest and cheapest way to pay off your student loans!</p>
        </header>
        
        <p>When paying off student loans, it can be hard to figure out which loans to prioritize to pay off your loans 
          both as quickly and cheaply as possible. Enter in your loans' principals and interest rates to find the optimal
          way to pay them off!</p>
      </div>
    );
  }
}

class LoanInput extends Component{

  constructor(props) {
    super(props);
    this.state = { 
      numberOfLoans: 1,
      loanInputs: [<LoanInputInterface key={1} loanID={1}/>],
      removeLoanEnabled: ''   // No value means that it is disabled
     };

     this.addNewLoan = this.addNewLoan.bind(this)
     this.removeLastLoan = this.removeLastLoan.bind(this)

  }

  addNewLoan(){

    // Add loan to the list of loans
    var loanTemp = this.state.loanInputs.slice()
    loanTemp.push(<LoanInputInterface key={this.state.numberOfLoans + 1} loanID={this.state.numberOfLoans + 1}/>);

    this.setState({
      numberOfLoans: this.state.numberOfLoans + 1,
      loanInputs: loanTemp,
      removeLoanEnabled: 'enabled'
    });

  }

  removeLastLoan(){

    console.log("In remove");
    // Remove the last item in array
    var loanTemp = this.state.loanInputs.slice()
    loanTemp.pop(); 
    
    // We need to see if the remove button needs to be disabled or not...
    if(this.state.numberOfLoans - 1 === 1){
      // There's only one loan left. Disable the removal of loans..
      // Set State
      this.setState({
        numberOfLoans: this.state.numberOfLoans - 1,
        loanInputs: loanTemp,
        removeLoanEnabled: ''
      });
    } else {
      // We have more than one loan left, thus we will allow the user to 
      // remove loans.
      this.setState({
        numberOfLoans: this.state.numberOfLoans - 1,
        loanInputs: loanTemp
      });
    }

    
  }

  render(){
    return(
      
      <div>

        <button type="button" id="removeLastButton" disabled={!this.state.removeLoanEnabled} onClick={this.removeLastLoan} className="btn btn-danger">Remove Last Loan</button>
        <button type="button" onClick={this.addNewLoan} className="btn btn-primary">Add Another Loan</button>

        <br/>
        <br/>

        <ReactCSSTransitionGroup
          transitionName="fade"
          transitionEnterTimeout={500}
          transitionLeaveTimeout={300}>
          {this.state.loanInputs}
        </ReactCSSTransitionGroup>
        
      </div>
    );
  }
}

class LoanInputInterface extends Component{

  constructor(props) {
    super(props);
    this.state = { 
      loanID: this.props.loanID,
      principalInputID: "principal" + this.props.loanID,
      interestInputID: "interest" + this.props.loanID,
      monthlyPaymentID: "monthlyPayment" + this.props.loanID
     };
  }

  render(){
    return(

      <div className="panel panel-default">

        <div className="panel-heading">
          <h3 className="panel-title">Loan {this.state.loanID}</h3>
        </div>

        <div className="panel-body">
          <div className="row">

            <div className="col-md-4">
              <div className="form-group">
                <label for={this.state.principalInputID}>Principal</label>
                <input type="number" min="0.00" className="form-control" id={this.state.principalInputID} defaultValue="1000"/>
              </div>
            </div>

            <div className="col-md-4">
              <div className="form-group">
                <label for={this.state.interestInputID}>Interest Rate</label>
                <input type="number" min="0.00" className="form-control" id={this.state.interestInputID} defaultValue="3.5"/>
              </div>
            </div>

            <div className="col-md-4">
              <div className="form-group">
                <label for={this.state.monthlyPaymentID}>Minimum Monthly Payment</label>
                <input type="number" min="0.00" className="form-control" id={this.state.monthlyPaymentID} defaultValue="100"/>
              </div>
            </div>
          </div>
        </div>

      </div>
    );
  }
}

class App extends Component{
  render() {
    return (
      <div>

        <Header/>

        <div className="container">         
          <LoanInput/>
        </div>

      </div>
    );
  }
}

export default App;


